<template>
  <div>
    <!-- 灰色头部 -->
    <flatGreyHead></flatGreyHead>

    <!-- 导航 -->
    <navBar cur="2"></navBar>

    <div class="dataHeaderBg">

      <div style="margin-top: 15px; margin-left:200px; margin-right:200px">
        <el-row>
          <el-col :span="3">
            <el-select v-model="v.searchDataType" placeholder="全部类型">
              <el-option label="全部类型" value=""></el-option>
              <el-option :label="t.name" :value="t.id" v-for="(t,i) in list.data_types" :key="i"></el-option>
            </el-select>
          </el-col>
          <el-col :span="16">
            <el-input placeholder="搜索你想要的数据" v-model="v.search_keyword" class="input-with-select">
              <el-button-group slot="append">
                <el-button icon="el-icon-search" @click="search"></el-button>
              </el-button-group>
            </el-input>
          </el-col>
          <el-col :span="2" :offset="1">
            <router-link to="/create">
              <el-button style="height:40px">新增数据集
              </el-button>
            </router-link>

          </el-col>
        </el-row>

      </div>
    </div>

    <!-- 筛选 -->
    <hr>

    <div class="blog-section">
      <div class="container">
        <div class="blog-top">

          <el-row v-if="list.data.length>0">
            <el-col :span="4" v-for="(d,di) in list.data" :key="di">
              <el-card :body-style="{ padding: '10px' }" shadow="hover" @click.native="dataDetail(d)" style="width:100%">
                <el-image :src="fileDownloadPath+'?fileCode='+ d.logo" class="datasetLogo">
                  <div slot="error" class="image-slot">
                    <img src="../assets/images/数据集成2.png" class="datasetLogo">
                  </div>

                </el-image>
                <!-- <img :src="fileDownloadPath+'?fileCode='+ d.logo" class="image"> -->
                <div style="padding: 14px;">
                  <span :title="d.title" style="font-size:10px" v-if="d.title">{{shortStr(d.title,5)}}</span>
                  <span title="暂无标题" style="font-size:10px" v-else>{{shortStr('暂无标题',5)}}</span>

                  <div class="bottom clearfix">
                    <!-- <p>{{ d.title }}</p> -->
                    <p v-if="d.dataSize">{{fsize(d.dataSize) }}</p>

                    <!-- 操作按钮 -->
                    <el-button-group v-if="false">
                      <!-- <el-button size="mini" round="" class="oper-btn">
                        <i class="el-icon-edit" @click="editDataset(d)"></i>
                      </el-button>
                      <el-button size="mini" round="" class="oper-btn">
                        <i class="el-icon-edit" @click="editDataset(d)"></i>
                      </el-button> -->
                      <el-button size="mini" round="">
                        <i class="el-icon-edit" @click="editDataset(d)"></i>
                      </el-button>
                      <el-button size="mini" round="">
                        <i class="el-icon-toilet-paper" @click="delDataset(d)"></i>
                      </el-button>
                    </el-button-group>

                  </div>
                </div>
              </el-card>
            </el-col>
          </el-row>
          <!-- 如果没有数据 -->
          <el-row v-if="list.data.length==0">
            <el-col :offset="10" :span="4">

              <img src="../assets/images/暂无数据.png">

            </el-col>
          </el-row>

        </div>
        <br>
      </div>
      <el-pagination :total="page.total" :page-size="page.pageSize" @current-change="pageChange"></el-pagination>
    </div>

    <div>

    </div>
  </div>
</template>


<script>
import { fetchList } from "@/api/product";

import navBar from "@/components/header/navBar";
import flatGreyHead from "@/components/header/flatGreyHead";

import {
  listDataHome,
  getDatasetTypes,
  searchDataset,
  delDataset
} from "@/api/datasets";

export default {
  components: {
    flatGreyHead,

    navBar
  },
  created() {
    this.search();

    this.reload_data_types();
  },
  methods: {
    // 删除数据
    delDataset(d) {
      this.$confirm("确定要删除吗", "提示", {
        confirmButtonText: "想好了",
        cancelButtonTexst: "点错了"
      }).then(() => {
        delDataset({ id: d.id }).then(() => {
          this.search();
        });
      });
    },
    // 编辑
    editDataset(d) {
      this.$store.commit("setHeap", {
        key: "ed_dataset_id",
        val: d.id
      });
      this.$router.push("/create");
    },
    sliderBarFormat(v) {
      let size = v;
      size = Math.pow(2, v);
      size = parseInt(size);
      size = this.fsize(size);
      return size;
    },
    sliderBarChange() {
      console.log(arguments);
    },
    search() {
      let para = {
        pageIndex: this.page.pageIndex,
        pageSize: this.page.pageSize,
        dataType: this.v.searchDataType
      };

      if (this.v.search_keyword) {
        para.title = this.v.search_keyword;
      }

      searchDataset(para).then(r => {
        if (r.code == 200) {
          this.list.data = r.data.content;
          this.page.total = r.data.totalElements;
        }
      });
    },
    reload_data_types() {
      getDatasetTypes().then(r => {
        if (r.code == 200) {
          this.list.data_types = r.data;
        }
      });
    },
    searchClick(d) {
      d = d.data;
      let url = "/search?kw=";
      url += d;
      this.$router.push(url);
    },
    dataDetail(d) {
      let url = "/detail?id=";
      url += d.id;
      this.$router.push(url);
      // window.open(url, "_blank");
    },
    pageChange(p) {
      let that = this;
      that.page.pageIndex = p;
      that.search();
    },

    reloadLoginStatus() {
      // 如果token有值，尝试读取$store
      if (getToken()) {
        this.info.roles = this.$store.getters.roles;
        this.info.name = this.$store.getters.name;
        this.info.avatar = this.$store.getters.avatar;
        this.info.token = this.$store.getters.token;

        // 如果有任意一个为空 则认为没有登陆
        return (
          this.info.roles &&
          this.info.name &&
          this.info.avatar &&
          this.info.token
        );
      } else {
        return false;
      }
    }
  },
  data() {
    return {
      currentDate: new Date(),
      v: {
        searchDataType: "",
        search_keyword: "",
        dataSize: [1, 9],
        marks: {
          0: "0°C",
          8: "8°C",
          37: "37°C",
          50: {
            style: {
              color: "#1989FA"
            },
            label: this.$createElement("strong", "50%")
          }
        }
      },
      info: {},
      page: { pageIndex: 1, pageSize: 20, total: 0 },

      list: {
        data: [],
        data_types: []
      }
    };
  }
};
</script>



<style scoped>
@import url(./../assets/css/style.css);
@import url(./../assets/plugins/bootstrap/dist/css/bootstrap.min.css);
.oper-btn {
  width: 30px;
}
.bottom {
  margin-top: 13px;
  line-height: 12px;
}

.datasetLogo {
  width: 300px;
  height: 300px;
  display: block;
}

.clearfix:before,
.clearfix:after {
  display: table;
  content: "";
}

.clearfix:after {
  clear: both;
}

.el-col.el-col-4.el-col-offset-0 {
  margin: 10px;
  cursor: pointer;
}

.cur {
  background: #ffa742;
  color: #fff;
  border-radius: 3px;
}
.dataHeaderBg {
  background: url("../assets/images/mnist.png");
  background-size: 100%;
  background-repeat: no-repeat;
  width: 100%;
  height: 300px;
  padding-top: 125px;
}

.el-select .el-input {
  width: 130px;
}
.input-with-select .el-input-group__prepend {
  background-color: #fff;
}
</style>



